<template>
  <base-item-info
    :titre="t('accueil.page.notifications.utilisationMmoire.titre')"
    :sous-titre="t('accueil.page.notifications.utilisationMmoire.sousTitre')"
    :texte-action="t('accueil.page.notifications.utilisationMmoire.texteAction', {pourcentage})"
    :info="info"
  >
    <template #contenu>
      <v-btn
        color="primary"
        variant="outlined"
        append-icon="mdi-content-save-outline"
        @click="() => $router.push('/favoris')"
      >
        {{ t('accueil.page.notifications.utilisationMmoire.btnAction') }}
      </v-btn>
    </template>
  </base-item-info>
</template>
<script setup lang="ts">
import {type InfoAvecId, type InfoUtilisationMmoire} from '/@/tat/infos';

import {computed} from 'vue';

import {_} from '@lassi-js/kilimukku-vue';
import BaseItemInfo from './BaseItemInfo.vue';

const props = defineProps<{
  info: InfoAvecId<InfoUtilisationMmoire>;
}>();

const {_} = _();
const {$: t} = _();

const pourcentage = computed(() => {
  const {quota, utilisation} = props.info.info.dtails;
  return (utilisation / quota) * 100;
});
</script>
