<template>
  <base-item-info
    :titre="t('accueil.page.notifications.connecterInstall.titre')"
    :sous-titre="t('accueil.page.notifications.connecterInstall.sousTitre')"
    :texte-action="t('accueil.page.notifications.connecterInstall.texteAction')"
    :info="info"
  >
    <template #contenu>
      <span>
        <p>
          {{ t('accueil.page.notifications.connecterInstall.dtails') }}
        </p>

        <v-list>
          <v-list-item
            :title="t('accueil.page.notifications.connecterInstall.avantages.connexion.titre')"
            :subtitle="
              t('accueil.page.notifications.connecterInstall.avantages.connexion.sousTitre')
            "
            prepend-icon="mdi-wifi"
          />
          <v-list-item
            :title="t('accueil.page.notifications.connecterInstall.avantages.sauvegardes.titre')"
            :subtitle="
              t('accueil.page.notifications.connecterInstall.avantages.sauvegardes.sousTitre')
            "
            prepend-icon="mdi-file-sync-outline"
          />
          <v-list-item
            :title="t('accueil.page.notifications.connecterInstall.avantages.mmoire.titre')"
            :subtitle="
              t('accueil.page.notifications.connecterInstall.avantages.mmoire.sousTitre')
            "
            prepend-icon="mdi-content-save"
          />
          <v-list-item
            :title="t('accueil.page.notifications.connecterInstall.avantages.plus.titre')"
            :subtitle="t('accueil.page.notifications.connecterInstall.avantages.plus.sousTitre')"
            prepend-icon="mdi-plus"
          />
        </v-list>
        <v-timeline
          class="my-6"
          side="end"
          truncate-line="both"
          :density="mdAndUp ? 'default' : 'compact'"
        >
          <v-timeline-item
            icon="mdi-download"
            dot-color="primary"
          >
            <div>
              <div class="text-h6">{{
                t('accueil.page.notifications.connecterInstall.tape1.titre')
              }}</div>
              <p>
                {{ t('accueil.page.notifications.connecterInstall.tape1.texte') }}
              </p>
              <v-btn
                class="my-2"
                color="primary"
                variant="tonal"
                append-icon="mdi-download"
                @click="() => ouvrirLien(urlTlcharger)"
              >
                {{ t('accueil.page.notifications.connecterInstall.tape1.tlcharger') }}
              </v-btn>
              <indice-installation-apple v-if="surPlateforme.so === 'mac'" />
            </div>
          </v-timeline-item>
          <v-timeline-item
            icon="mdi-lan-connect"
            dot-color="primary"
          >
            <div>
              <div class="text-h6">{{
                t('accueil.page.notifications.connecterInstall.tape2.titre')
              }}</div>
              <p>
                {{ t('accueil.page.notifications.connecterInstall.tape2.texte') }}
              </p>
              <inviter-dispositif>
                <template #activator="{props: propsActivateur}">
                  <v-btn
                    v-bind="propsActivateur"
                    class="mt-2"
                    color="primary"
                    variant="tonal"
                    :append-icon="connectAuMoinsUnOrdi ? 'mdi-check' : 'mdi-lan-connect'"
                    :active="!connectAuMoinsUnOrdi"
                  >
                    {{
                      connectAuMoinsUnOrdi
                        ? t('accueil.page.notifications.connecterInstall.tape2.connect')
                        : t('accueil.page.notifications.connecterInstall.tape2.code')
                    }}
                  </v-btn>
                </template>
              </inviter-dispositif>
            </div>
          </v-timeline-item>
        </v-timeline>
      </span>
    </template>
  </base-item-info>
</template>
<script setup lang="ts">
import type {InfoAvecId, InfoInstaller} from '/@/tat/infos';

import {onMounted} from 'vue';

import {suivre} from '@constl/vue';
import {_} from '@lassi-js/kilimukku-vue';
import {computed, ref} from 'vue';
import {useDisplay} from 'vuetify/lib/framework.mjs';
import InviterDispositif from '../../compte/InviterDispositif.vue';
import {utiliserConstellation} from '../../utils';
import BaseItemInfo from './BaseItemInfo.vue';
import IndiceInstallationApple from '/@/components/accueil/infos/IndiceInstallationApple.vue';
import {obtenirDernireVersion} from '/@/components/accueil/infos/utils';
import {URL_TLCHARGEMENTS} from '/@/consts';
import {ouvrirLien, plateforme} from '/@/utils';

defineProps<{
  info: InfoAvecId<InfoInstaller>;
}>();

const {mdAndUp} = useDisplay();
const {_} = _();
const {$: t} = _();

const constl = utiliserConstellation();

const dispositifs = suivre(constl.suivreNomsDispositifs);
const connectAuMoinsUnOrdi = computed(() => {
  return (
    dispositifs.value &&
    !!Object.values(dispositifs.value).find(d => d.type === 'ordinateur' || d.type === 'serveur')
  );
});

const surPlateforme = plateforme();
const surOrdi = surPlateforme.so && ['linux', 'windows', 'mac'].includes(surPlateforme.so);
const urlVersionPlusRcente = ref<string>();
onMounted(async () => {
  const v = await obtenirDernireVersion();
  urlVersionPlusRcente.value = v?.urlTlchargement;
});
const urlTlcharger = computed<string>(() => {
  if (surOrdi) {
    return urlVersionPlusRcente.value || URL_TLCHARGEMENTS;
  }
  return URL_TLCHARGEMENTS;
});
</script>
