<template>
  <selecteur-objet
    :multiples="multiples"
    :originaux="originales"
    :interdits="interdits"
    :resultats-recherche="rsultats"
    :on-travaille="onTravaille"
    :texte-etiquette-recherche="t('membres.recherche.tiquette')"
    :texte-aucun-rsultat="t('membres.recherche.aucunRsultat')"
    @selectionnee="ids => (idsProfilsSlectionnes = ids)"
    @requete-modifiee="r => (requte = r)"
  >
    <template #rsultat="{rsultat, click}">
      <ResultatRechercheProfil
        :rsultat="rsultat"
        @click="click"
      />
    </template>
    <template #jeton-objet="{id, deselectionner}">
      <carte-membre :id="id">
        <template #activator="{props: propsActivateur}">
          <jeton-membre
            :compte="id"
            v-bind="{props: propsActivateur}"
          >
            <v-icon
              class="ms-2"
              icon="mdi-close"
              size="small"
              variant="flat"
              @click="() => deselectionner({id})"
            />
          </jeton-membre>
        </template>
      </carte-membre>
    </template>
  </selecteur-objet>
</template>
<script setup lang="ts">
import {rechercher} from '@constl/vue';
import {ref, watchEffect} from 'vue';

import SelecteurObjet from '/@/components/communs/SlecteurObjet.vue';
import ResultatRechercheProfil from '/@/components/recherche/RsultatRechercheProfil.vue';

import {utiliserConstellation} from '/@/components/utils';

import {_} from '@lassi-js/kilimukku-vue';
import CarteMembre from './CarteMembre.vue';
import JetonMembre from './JetonMembre.vue';

defineProps<{multiples: boolean; originales?: string[]; interdits?: string[]}>();
const mettre = defineEmits<{
  (: 'selectionnee', idsProfils: string[]): void;
}>();

const {_} = _();
const {$: t} = _();

const constl = utiliserConstellation();

// Slection
const idsProfilsSlectionnes = ref<string[]>([]);
watchEffect(() => {
  mettre('selectionnee', idsProfilsSlectionnes.value);
});

// Contrles recherche
const requte = ref('');
const {rsultats, onTravaille} = rechercher(constl.recherche.rechercherProfilsSelonTexte, {
  texte: requte,
});
</script>
