<template>
  <v-row>
    <v-col cols="12">
      <p class="text-h4">
        {{ t('accueil.page.graphiques.titre') }}
      </p>
    </v-col>
    <v-col cols="12">
      <v-card
        class="text-start"
        variant="elevated"
      >
        <v-card-text>
          <graphique-historique-ligne
            :titre="t('accueil.page.graphiques.bds')"
            :vals="obtHistorique('mesBds')"
            couleur="#6e40aa"
          />
          <graphique-historique-ligne
            :titre="t('accueil.page.graphiques.projets')"
            couleur="#bf3caf"
          />
          <graphique-historique-ligne
            :titre="t('accueil.page.graphiques.nues')"
            :vals="obtHistorique('mesNues')"
            couleur="#fe4b83"
          />
          <graphique-historique-ligne
            :titre="t('accueil.page.graphiques.rplications')"
            couleur="#ff7847"
          />
          <graphique-historique-ligne
            :titre="t('accueil.page.graphiques.connaissances')"
            couleur="#e2b72f"
          >
            <template #action>
              <div class="mx-auto mb-6 text-center text-disabled">
                <GestionConnaissances>
                  <template #activator="{props}">
                    <v-btn
                      v-bind="props"
                      variant="outlined"
                      prepend-icon="mdi-plus"
                    >
                      {{ t('accueil.page.graphiques.ajouterConnaissances') }}
                    </v-btn>
                  </template>
                </GestionConnaissances>
              </div>
            </template>
          </graphique-historique-ligne>
          <graphique-historique-ligne
            :titre="t('accueil.page.graphiques.rseau')"
            couleur="#aff05b"
          />
        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>
<script lang="ts" setup>
import {storeToRefs} from 'pinia';
import {computed, onMounted, ref, watchEffect} from 'vue';

import {rechercher, suivre} from '@constl/vue';
import {_} from '@lassi-js/kilimukku-vue';

import {utiliserConstellation} from '../utils';
import {utiliserHistorique} from '/@/tat/historique';

import GraphiqueHistoriqueLigne from './GraphiqueHistoriqueLigne.vue';
import GestionConnaissances from '/@/components/rseau/GestionConnaissances.vue';

const {_} = _();
const {$: t} = _();
const historique = utiliserHistorique({});

const constl = utiliserConstellation();

const {obtHistorique} = storeToRefs(historique);

const imageFond = ref<string>();
onMounted(async () => {
  imageFond.value = (await import('/@/assets/fonds/trianglify-lowres.png')).default;
});

const bds = suivre(constl.bds.suivreBds);
const nBds = computed(() => bds.value?.length);
watchEffect(() => {
  historique.observer({clef: 'mesBds', val: nBds.value});
});

const projets = suivre(constl.projets.suivreProjets);
const nProjets = computed(() => projets.value?.length);
watchEffect(() => {
  historique.observer({clef: 'mesProjets', val: nProjets.value});
});

const nues = suivre(constl.nues.suivreNues);
const nNues = computed(() => nues.value?.length);
watchEffect(() => {
  historique.observer({clef: 'mesNues', val: nNues.value});
});

const membresFiables = suivre(constl.rseau.suivreFiables);
const nMembresFiables = computed(() => membresFiables.value?.length);
watchEffect(() => {
  historique.observer({clef: 'mesConnaissances', val: nMembresFiables.value});
});

const {rsultats: membresRseau} = rechercher(constl.rseau.suivreComptesRseau);
const nMembresRseau = computed(() => membresRseau.value?.length);
watchEffect(() => {
  historique.observer({clef: 'monRseau', val: nMembresRseau.value});
});
</script>
