<template>
  <v-list-item
    :subtitle="texteVuIlYA"
    :prepend-avatar="srcImg || imgDfaut"
    @click="$router.push(encodeURI(`/donnes/${typeObjet}/${encodeURIComponent(id)}`))"
  >
    <template #title>
      <span v-if="noms">
        {{ nomTraduit || t(sansNom) }}
        <v-avatar>
          <v-icon>{{ icneTypeItem }}</v-icon>
        </v-avatar>
      </span>
      <span v-else>
        <v-skeleton-loader type="text" />
      </span>
    </template>
    <template #append>
      <v-btn
        class="mx-2"
        variant="flat"
        icon="mdi-close"
        @click="() => effacerDeLHistorique()"
      />
    </template>
  </v-list-item>
</template>
<script setup lang="ts">
import {computed} from 'vue';

import {suivre} from '@constl/vue';
import {_, _} from '@lassi-js/kilimukku-vue';
import {utiliserIlYA} from '../membres/utils';
import {icneObjet, utiliserConstellation} from '../utils';
import {utiliserImagesDco} from '/@/composables/images';
import {utiliserHistoriqueDocuments} from '/@/tat/historiqueDocuments';

const props = defineProps<{id: string; a: number}>();

const {_} = _();
const {$: t} = _();
const {_} = _();
const {obtImageDco} = utiliserImagesDco();

const constl = utiliserConstellation();

// Solution temporaire pour Constellation qui ne sait pas de quel type est l'objet
const noms = suivre(constl.motsClefs.suivreNomsMotClef, {idMotClef: props.id});
const nomTraduit = _(noms);

const historiqueDocuments = utiliserHistoriqueDocuments();

const typeObjet = suivre(constl.suivreTypeObjet, {idObjet: props.id});
const icneTypeItem = computed(() => {
  const icne = icneObjet(typeObjet.value);
  return icne || 'mdi-file-document-outline';
});
const sansNom = computed(() => {
  return typeObjet.value ? `${typeObjet.value}s.sansNom` : 'communs.sansNom';
});

//  faire : diffrencier par type d'objet
const image = suivre(constl.bds.suivreImage, {idBd: props.id});
const srcImg = computed(() => {
  if (image.value) {
    return URL.createObjectURL(new Blob([image.value], {type: 'image'}));
  } else {
    return undefined;
  }
});
const imgDfaut = obtImageDco('logoBD');

const {texte: texteVuIlYA} = utiliserIlYA({vu: props.a, t});

const effacerDeLHistorique = () => historiqueDocuments.effacer({id: props.id});
</script>
