<template>
  <v-card
    class="text-start"
    style="height: 100%"
  >
    <v-card-item>
      <v-card-title>
        <v-avatar><v-icon>mdi-transit-connection-horizontal</v-icon></v-avatar>
        {{ t('accueil.page.connectivit.serveurLocal.titre') }}
      </v-card-title>
    </v-card-item>
    <v-card-text>
      <v-list>
        <v-list-item>
          <v-switch
            class="mx-2"
            density="compact"
            hide-details
            disabled
            true-icon="mdi-check"
            false-icon="mdi-close"
            :label="
              tatServeur?.tat === 'actif'
                ? t('accueil.page.connectivit.serveurLocal.activ')
                : t('accueil.page.connectivit.serveurLocal.dsactiv')
            "
          />
        </v-list-item>
        <v-list-item prepend-icon="mdi-lan">
          {{ t('accueil.page.connectivit.serveurLocal.requtes', nRequtes || 0) }}
        </v-list-item>
        <div class="text-center">
          <controles-serveur-local>
            <template #activator="{props: propsActivateur}">
              <v-btn
                v-bind="propsActivateur"
                class="mx-auto"
                variant="outlined"
                prepend-icon="mdi-toggle-switch-outline"
              >
                {{ t('accueil.page.connectivit.serveurLocal.grer') }}
              </v-btn>
            </template>
          </controles-serveur-local>
        </div>
      </v-list>
    </v-card-text>
  </v-card>
</template>
<script setup lang="ts">
import {suivre} from '@constl/vue';
import {_} from '@lassi-js/kilimukku-vue';
import {computed} from 'vue';
import {utiliserServeurLocalConstellation} from '../utils';

import ControlesServeurLocal from './ContrlesServeurLocal.vue';

const serveurLocal = utiliserServeurLocalConstellation();

const {_} = _();

const {$: t} = _();

// Serveur local
const requtesServeurLocal = suivre(serveurLocal.suivreRequtesAuthServeur.bind(serveurLocal));
const nRequtes = computed(() => requtesServeurLocal.value?.length);

const tatServeur = suivre(serveurLocal.suivretatServeur.bind(serveurLocal));
</script>
