<template>
  <v-dialog v-model="dialogue">
    <template #activator="{props: propsActivateur}">
      <slot
        name="activator"
        v-bind="{props: propsActivateur}"
      ></slot>
    </template>
    <v-card
      :max-width="mdAndUp ? 700 : 300"
      class="mx-auto"
    >
      <v-card-item>
        <v-card-title class="d-flex">
          {{ t('rseau.connaissances.titreCarte') }}
        </v-card-title>
      </v-card-item>
      <v-card-text>
        <v-tabs v-model="onglet">
          <v-tab
            value="confiance"
            append-icon="mdi-hands-pray"
          >
            {{ t('membres.confiance.membresDeConfiance') }}
          </v-tab>
          <v-tab
            value="bloqu"
            append-icon="mdi-cancel"
          >
            {{ t('membres.confiance.membresBloqus') }}
          </v-tab>
        </v-tabs>
        <v-window v-model="onglet">
          <v-window-item value="confiance">
            <v-list>
              <selecteur-membre
                :multiples="true"
                :interdits="idMonCompte ? [idMonCompte] : []"
                :originales="confiance?.map(c => c.idCompte)"
                @selectionnee="ids => (nouvellesDeConfiance = ids)"
              >
              </selecteur-membre>
              <item-membre
                v-for="c in confiance"
                :key="c.idCompte"
                :compte="c.idCompte"
                montrer-anonymes
              >
                <template #append>
                  <v-btn
                    icon="mdi-delete"
                    @click="() => nePlusFaireConfiance(c.idCompte)"
                  />
                </template>
              </item-membre>
            </v-list>
          </v-window-item>
          <v-window-item value="bloqu">
            <v-list>
              <selecteur-membre
                :multiples="false"
                :interdits="idMonCompte ? [idMonCompte] : []"
                :originales="bloqus?.map(c => c.idCompte)"
                @selectionnee="ids => (nouvellesBloques = ids)"
              >
              </selecteur-membre>
              <v-checkbox
                v-model="bloquerNouveauxPriv"
                color="primary"
              >
                <template #label>
                  {{ t('membres.confiance.bloquerPriv') }}
                  <v-tooltip location="bottom">
                    <template #activator="{props: propsActivateur}">
                      <v-icon
                        v-bind="propsActivateur"
                        icon="mdi-information-outline"
                        end
                      />
                    </template>
                    {{ t('membres.confiance.indiceBloquerPriv') }}
                  </v-tooltip>
                </template>
              </v-checkbox>
              <item-membre
                v-for="b in bloqus"
                :key="b.idCompte"
                :compte="b.idCompte"
                montrer-anonymes
              >
                <template #append>
                  <v-switch
                    :label="t('priv')"
                    :value="b.priv"
                    @update:model-value="
                      b.priv ? bloquerPublique(b.idCompte) : bloquerPriv(b.idCompte)
                    "
                  />
                  <v-btn
                    icon="mdi-delete"
                    @click="() => dbloquer(b.idCompte)"
                  />
                </template>
              </item-membre>
            </v-list>
          </v-window-item>
        </v-window>
      </v-card-text>
      <v-divider />
      <v-card-actions>
        <v-spacer />
        <v-btn
          variant="outlined"
          @click="fermer"
        >
          {{ t('communs.annuler') }}
        </v-btn>
        <v-btn
          color="primary"
          variant="outlined"
          append-icon="mdi-check"
          @click="sauvegarder"
        >
          {{ t('communs.sauvegarder') }}
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script setup lang="ts">
import {suivre} from '@constl/vue';
import {ref} from 'vue';

import {_} from '@lassi-js/kilimukku-vue';
import {useDisplay} from 'vuetify/lib/framework.mjs';
import ItemMembre from '../membres/ItemMembre.vue';
import {utiliserConstellation} from '../utils';
import SelecteurMembre from '/@/components/membres/SlecteurMembre.vue';

const {_} = _();
const {$: t} = _();
const {mdAndUp} = useDisplay();

const constl = utiliserConstellation();

const dialogue = ref(false);
const onglet = ref<'confiance' | 'bloqu'>();

const idMonCompte = suivre(constl.suivreIdCompte, {});
const confiance = suivre(constl.rseau.suivreRelationsImmdiates, {});
const bloqus = suivre(constl.rseau.suivreBloqus, {});

const nouvellesDeConfiance = ref<string[]>([]);
const nouvellesBloques = ref<string[]>([]);
const bloquerNouveauxPriv = ref(false);

const dbloquer = async (idCompte: string) => {
  constl.rseau.dbloquerMembre({idCompte});
};

const bloquerPriv = async (idCompte: string) => {
  constl.rseau.bloquerMembre({idCompte, priv: true});
};

const bloquerPublique = async (idCompte: string) => {
  constl.rseau.bloquerMembre({idCompte, priv: false});
};

const nePlusFaireConfiance = async (idCompte: string) => {
  constl.rseau.nePlusFaireConfianceAuMembre({idCompte});
};

const sauvegarder = async () => {
  await Promise.all(
    nouvellesDeConfiance.value.map(c => constl.rseau.faireConfianceAuMembre({idCompte: c})),
  );
  await Promise.all(
    nouvellesBloques.value.map(b =>
      constl.rseau.bloquerMembre({idCompte: b, priv: bloquerNouveauxPriv.value}),
    ),
  );
};

const fermer = () => {
  nouvellesDeConfiance.value = [];
  nouvellesBloques.value = [];
  dialogue.value = false;
};
</script>
