<template>
  <base-item-info
    :titre="t('accueil.page.notifications.protgerDonnes.titre')"
    :sous-titre="t('accueil.page.notifications.protgerDonnes.sousTitre')"
    :texte-action="t('accueil.page.notifications.protgerDonnes.texteAction')"
    :info="info"
  >
    <template #contenu>
      <p class="mb-4">
        <span class="mb-4 font-weight-bold">{{ t('accueil.initialiserCompte.plusDInfo') }}</span>
        <v-btn
          :icon="dtailsPersister ? 'mdi-chevron-up' : 'mdi-chevron-down'"
          size="xsmall"
          variant="flat"
          @click="() => (dtailsPersister = !dtailsPersister)"
        ></v-btn>
        <v-expand-transition>
          <div v-show="dtailsPersister">
            <p class="mb-4"> {{ t('accueil.initialiserCompte.textePersister.1') }} </p>
            <p class="mb-4"> {{ t('accueil.initialiserCompte.textePersister.2') }} </p>
            <p class="mb-4"> {{ t('accueil.initialiserCompte.textePersister.3') }} </p>
          </div>
        </v-expand-transition>
      </p>
      <v-btn
        color="primary"
        variant="outlined"
        append-icon="mdi-content-save-outline"
        @click="() => persisterDonnes()"
      >
        {{ t('accueil.page.notifications.protgerDonnes.protger') }}
      </v-btn>
    </template>
  </base-item-info>
</template>
<script setup lang="ts">
import {type InfoAvecId, type InfoProtgerDonnnes, utilisertatInfos} from '/@/tat/infos';

import {_} from '@lassi-js/kilimukku-vue';
import {ref} from 'vue';
import BaseItemInfo from './BaseItemInfo.vue';

const props = defineProps<{
  info: InfoAvecId<InfoProtgerDonnnes>;
}>();

const tatInfos = utilisertatInfos();

const {_} = _();
const {$: t} = _();

const persisterDonnes = async () => {
  const persistes = await navigator.storage.persist();
  if (persistes) {
    tatInfos.effacerInfo(props.info.id);
  }
};

const dtailsPersister = ref(false);
</script>
