<template>
  <v-row>
    <v-col cols="12">
      <p class="text-h4">
        {{ t('accueil.page.rcents.titre') }}
      </p>
    </v-col>
    <v-col :cols="mdAndUp ? 4 : smAndUp ? 6 : 12">
      <v-card
        class="text-start"
        style="height: 100%"
      >
        <v-card-item>
          <v-card-title>
            <v-avatar><v-icon>mdi-plus</v-icon></v-avatar>
            {{ t('accueil.page.rcents.nouveau') }}
          </v-card-title>
        </v-card-item>
        <v-card-text>
          <v-list>
            <nouvelle-bd>
              <template #activator="{props: propsActivateur}">
                <v-list-item
                  v-bind="propsActivateur"
                  :prepend-icon="icneObjet('bd')"
                  :title="t('accueil.page.rcents.nouvelleBd')"
                />
              </template>
            </nouvelle-bd>
            <nouveau-projet>
              <template #activator="{props: propsActivateur}">
                <v-list-item
                  v-bind="propsActivateur"
                  :prepend-icon="icneObjet('projet')"
                  :title="t('accueil.page.rcents.nouveauProjet')"
                />
              </template>
            </nouveau-projet>
            <nouvelle-nuee>
              <template #activator="{props: propsActivateur}">
                <v-list-item
                  v-bind="propsActivateur"
                  :prepend-icon="icneObjet('nue')"
                  :title="t('accueil.page.rcents.nouvelleNue')"
                />
              </template>
            </nouvelle-nuee>
          </v-list>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col
      v-for="r in rcentsMontrer"
      :key="r.id"
      :cols="mdAndUp ? 4 : smAndUp ? 6 : 12"
    >
      <carte-document-recent
        :id="r.id"
        :a="r."
      />
    </v-col>
    <v-col
      v-if="rcentsEnPlus.length"
      :cols="mdAndUp ? 4 : smAndUp ? 6 : 12"
    >
      <v-dialog>
        <template #activator="{props: propsActivateur}">
          <v-card
            v-bind="propsActivateur"
            class="d-flex align-center text-center"
            variant="flat"
            height="100%"
          >
            <span class="pa-4 ma-auto text-h4 text-center">
              {{ t('accueil.page.rcents.voirPlus') }}
              <br />
              <p class="text-h2 my-4">
                {{ t('accueil.page.rcents.plusN', [rcentsEnPlus.length]) }}
              </p>
            </span>
          </v-card>
        </template>
        <v-card>
          <v-card-item>
            <v-card-title>
              {{ t('accueil.page.rcents.documentsRcents') }}
            </v-card-title>
          </v-card-item>
          <v-card-text style="overflow-y: auto">
            <v-list>
              <item-document-recent
                v-for="rcent in rcents"
                :id="rcent.id"
                :key="rcent.id"
                :a="rcent."
              />
            </v-list>
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-col>
    <v-col
      v-if="!rcents.length"
      :cols="mdAndUp ? 8 : smAndUp ? 6 : 12"
    >
      <v-card
        class="d-flex align-center text-center"
        variant="flat"
        height="230"
      >
        <span class="pa-4 ma-auto text-h4 text-center text-disabled">
          {{ t('accueil.page.rcents.aucunRcent') }}
        </span>
      </v-card>
    </v-col>
  </v-row>
</template>
<script lang="ts" setup>
import {computed, ref} from 'vue';
import {useDisplay} from 'vuetify';

import {_} from '@lassi-js/kilimukku-vue';
import {storeToRefs} from 'pinia';

import {utiliserHistoriqueDocuments} from '/@/tat/historiqueDocuments';

import NouvelleBd from '../bds/NouvelleBd.vue';
import NouvelleNuee from '../nues/NouvelleNue.vue';
import NouveauProjet from '../projets/NouveauProjet.vue';
import CarteDocumentRecent from './CarteDocumentRcent.vue';
import ItemDocumentRecent from './ItemDocumentRcent.vue';
import {icneObjet} from '/@/components/utils';

const {_} = _();
const {$: t} = _();

const {mdAndUp, smAndUp} = useDisplay();

const tatHistoriqueDocuments = utiliserHistoriqueDocuments();
const {rcents} = storeToRefs(tatHistoriqueDocuments);

const nMaxRcents = ref(5);
const rcentsMontrer = computed(() => {
  return rcents.value.length === nMaxRcents.value
    ? rcents.value
    : rcents.value.slice(0, nMaxRcents.value - 1);
});

const rcentsEnPlus = computed(() => {
  return rcents.value.length <= nMaxRcents.value
    ? []
    : rcents.value.slice(nMaxRcents.value - 1);
});
</script>
