<template>
  <v-dialog v-model="dialogue">
    <template #activator="{props: propsActivateur}">
      <slot
        name="activator"
        v-bind="{props: propsActivateur}"
      ></slot>
    </template>
    <v-card
      class="mx-auto"
      :min-width="mdAndUp ? 500 : 300"
    >
      <v-card-item>
        <v-card-title class="d-flex">
          {{ t('accueil.page.connectivit.rseau.connexion.titre') }}
          <v-spacer />
          <v-btn
            icon="mdi-close"
            size="small"
            variant="flat"
            @click="dialogue = false"
          />
        </v-card-title>
      </v-card-item>
      <v-card-text class="text-center">
        <div class="my-2">
          <div v-if="mode === 'numriser'">
            <v-progress-circular v-if="enCoursDeConnexion"></v-progress-circular>
            <p v-else-if="erreurConnexion">
              Erreur de connexion.
              <v-btn @click="() => (erreurConnexion = false)">Ressayer</v-btn>
            </p>
            <lecteur-code-r2
              v-else-if="!connecte"
              :schema="schmaCodeR2Connexion"
              @detecte="({valJSON}) => lorsqueDtect({adresses: valJSON?.adresses})"
            />
            <p v-else>Connect  {{ connecte }}.</p>
          </div>
          <qrcode-vue
            v-else
            :value="adressesTexte"
            :size="200"
          />
        </div>
        <v-btn-toggle v-model="mode">
          <v-btn value="afficher">
            <v-icon start>mdi-qrcode</v-icon>
            {{ t('accueil.connexion.afficherCode') }}
          </v-btn>
          <v-btn value="numriser">
            <v-icon start>mdi-qrcode-scan</v-icon>
            {{ t('accueil.connexion.numriserCode') }}
          </v-btn>
        </v-btn-toggle>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>
<script setup lang="ts">
import {_} from '@lassi-js/kilimukku-vue';
import {ref, watchEffect} from 'vue';
import {useDisplay} from 'vuetify';

import {suivre} from '@constl/vue';
import QrcodeVue from 'qrcode.vue';
import {computed} from 'vue';
import {utiliserConstellation} from '../utils';

import {schmaCodeR2Connexion} from '/@/codesR2';
import LecteurCodeR2 from '/@/components/communs/LecteurCodeR2.vue';

const {_} = _();
const {$: t} = _();
const {mdAndUp} = useDisplay();
const constl = utiliserConstellation();

// Contrles
const dialogue = ref(false);
const mode = ref<'afficher' | 'numriser'>('afficher');

// Nos adresses
const adresses = suivre(constl.rseau.suivreMesAdresses);

const adressesTexte = computed(() => {
  return adresses.value ? JSON.stringify(adresses.value.map(a => a.toString())) : undefined;
});

const adressesDtectes = ref<string[]>();

function lorsqueDtect({adresses}: {adresses?: string[]}) {
  adressesDtectes.value = adresses;
}

const connecte = ref<string>();
const enCoursDeConnexion = ref(false);
const erreurConnexion = ref(false);
watchEffect(async () => {
  connecte.value = undefined;
  erreurConnexion.value = false;
  if (adressesDtectes.value) {
    enCoursDeConnexion.value = true;
    try {
      for (const a of adressesDtectes.value) {
        await constl.rseau.connecterAdresse({adresse: a});
        connecte.value = a;
        erreurConnexion.value = false;
        enCoursDeConnexion.value = false;
        break;
      }
    } catch {
      erreurConnexion.value = true;
    }
  }
});
</script>
