<template>
  <v-dialog v-model="dialogue">
    <template #activator="{props: propsActivateur}">
      <slot
        name="activator"
        v-bind="{props: propsActivateur}"
      ></slot>
    </template>
    <v-card
      class="mx-auto"
      :min-width="mdAndUp ? 500 : 300"
    >
      <v-card-item>
        <v-card-title class="d-flex">
          {{ t('accueil.page.connectivit.serveurLocal.titre') }}
          <v-spacer />
          <v-btn
            icon="mdi-close"
            size="small"
            variant="flat"
            @click="dialogue = false"
          />
        </v-card-title>
      </v-card-item>
      <v-card-text>
        <v-icon start>mdi-information-outline</v-icon>Vous pouvez activer l'accs au nud local afin
        de pouvoir accder Constellation  partir d'applications tirces (par exemple, Python, R).
        <v-switch
          v-model="choixActif"
          :color="choixActif ? 'primary' : 'secondary'"
          :label="actif ? 'Accs activ' : 'Accs dsactiv'"
        />
        <v-text-field
          v-model="choixPort"
          label="Port"
          variant="outlined"
        />
        <v-textarea
          v-if="codeSecret"
          v-model="codeSecret"
          label="Code secret"
          readonly
          variant="outlined"
          auto-grow
          :append-inner-icon="codeSecretCopi ? 'mdi-check' : 'mdi-content-copy'"
          @click:append-inner="() => copierCodeSecret()"
        >
        </v-textarea>
        <v-btn
          :loading="miseJourEnCours"
          :disabled="choixPort === port"
          @click="() => dmarrerServeur()"
        />
        <v-list>
          <v-list-item
            v-for="r in requtes"
            :key="r"
          >
            {{ r }}
          </v-list-item>
        </v-list>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>
<script setup lang="ts">
import {suivre} from '@constl/vue';
import {computed, ref, watch, watchEffect} from 'vue';
import {useDisplay} from 'vuetify';

import {_} from '@lassi-js/kilimukku-vue';
import {utiliserServeurLocalConstellation} from '/@/components/utils';
import {copier} from '/@/utils';

const {_} = _();
const {$: t} = _();
const {mdAndUp} = useDisplay();

const serveurLocal = utiliserServeurLocalConstellation();

// Navigation
const dialogue = ref(false);

// Serveur local - statut
const statut = suivre(serveurLocal.suivretatServeur.bind(serveurLocal));
const actif = computed(() => statut.value?.tat === 'actif');
const codeSecret = computed(() =>
  statut.value?.tat === 'actif' ? statut.value.dtails.codeSecret : undefined,
);
const port = computed(() =>
  statut.value?.tat === 'actif' ? statut.value.dtails.port : undefined,
);

const choixActif = ref(statut.value?.tat === 'actif');
watchEffect(() => (choixActif.value = statut.value?.tat === 'actif'));
const choixPort = ref(port.value);
watchEffect(() => (choixPort.value = port.value));

const miseJourEnCours = ref(false);
const dmarrerServeur = async () => {
  if (statut.value?.tat === 'actif' && choixPort.value === port.value) return;
  miseJourEnCours.value = true;
  await serveurLocal.initialiser(choixPort.value);
  miseJourEnCours.value = false;
};

watchEffect(async () => {
  if (choixActif.value) {
    await dmarrerServeur();
  }
});

const codeSecretCopi = ref(false);
const copierCodeSecret = async () => {
  await copier(codeSecret.value || '');
  codeSecretCopi.value = true;
};
watch(codeSecret, () => {
  codeSecretCopi.value = false;
});

// Serveur local - requtes
const requtes = suivre(serveurLocal.suivreRequtesAuthServeur.bind(serveurLocal));
</script>
